import * as React from "react";
import { Link } from "gatsby";

const ContactUs = ({ pageTitle, children }) => {
  return (
    <div className="row">
      <div className="col-12">
        <div className="text-center border-top bg-dark text-white">
          <p className="py-4 fs-1">Want to work together</p>
          <button
            type="button"
            className="btn btn-warning btn-lg mb-4 fw-bold rounded-pill fs-4"
          >
            Contact us
          </button>
        </div>
      </div>
    </div>
  );
};

export default ContactUs;
